import React from "react";
import { Link } from "react-router-dom";
import ContactCard from "./ContactCard";

const ContactList = (props) => {
    const deleteContactHandler =(id) =>{
        props.getContactId(id);
    }
    console.log(props);
    const renderContactList = props.contacts.map((contacts) => {
        return(
            <ContactCard contact={contacts} clickHandler={deleteContactHandler}/>
        )
    });
    return(
        <div className="ui celled list">
            <h2 style={{marginTop: "50px"}}>Contact List</h2>
            <Link to="/add"><button style={{marginTop: "-44px"}} className="ui primary right floated button">Add Contact</button></Link>
            {renderContactList}
        </div>
    );
}
export default ContactList;